export default {
  name: 'OrderCart',
  data () {
    return {
      total: 0,
      selected: [],
      showCart: false
    }
  },
  computed: {
    carts () {
      return this.$store.state.carts
    },
    totalPrice () {
      return this.$store.getters.totalPrice
    },
    totalCount () {
      return this.$store.getters.totalCount
    },
    isShow () {
      return this.totalCount > 0
    }
  },
  methods: {
    handleHide () {
      this.showCart = false
    },
    handleShow () {
      console.log('show')
      if (this.showCart === false && this.totalCount > 0) {
        this.showCart = true
      }
    },
    addNumber (dinner) {
      dinner.number++
      this.$store.commit('addGoods', dinner)
      this.$store.dispatch('getGoodsList')
    },
    reduceNumber (dinner) {
      dinner.number--
      this.$store.commit('reduceGoods', dinner)
      this.$store.dispatch('getGoodsList')
      if (this.totalCount <= 0) {
        this.showCart = false
      }
    },
    handleClear () {
      this.$store.commit('clearCarts')
    },
    handlePay () {
      console.log('click')
      this.$emit('toPay')
    }
  },
  mounted () {
    console.log(this.carts)
  }
}



