export default {
  name: 'OrderFood',
  props: {
    dinner: {
      type: Object
    }
  },
  data () {
    return {

    }
  },
  computed: {
    food () {
      return this.dinner
    }
  },
  methods: {
    handleAdd (dinner, index, pindex) {
      dinner.number++
      this.$store.commit('addGoods', dinner)
    },
    handleReduce (dinner, index, pindex) {
      dinner.number--
      this.$store.commit('reduceGoods', dinner)
    }
  }
}



