import CommonHeader from '@/pages/common/header.vue'
import Cart from '@/pages/order/components/Cart'
import Dinners from '@/pages/order/components/Dinners'
import OrderConfirm from '@/pages/order/components/Confirm'
import OrderPay from '@/pages/common/Pay.vue'
export default {
  name: 'Order',
  data () {
    return {
      showConfirm: false,
      showPay: false
    }
  },
  components: {
    Cart,
    Dinners,
    CommonHeader,
    OrderConfirm,
    OrderPay
  },
  methods: {
    handleShowConfirm () {
      this.showConfirm = true
    },
    handleHideConfirm () {
      this.showConfirm = false
    },
    handleHidePay () {
      this.showPay = false
      this.$router.push('/')
    },
    handleSubmit () {
      this.showPay = true
      this.showConfirm = false
      localStorage.removeItem('carts')
      this.$store.commit('clearCarts')
    },
    handlePaySuccess () {
      this.showPay = false
      this.showConfirm = false
    }
  },
  mounted () {
    this.$store.commit('getCarts')
    this.$store.dispatch('getGoodsList')
  }
}



