import BScroll from 'better-scroll'
import OrderFood from '@/pages/order/components/Food'
export default {
  name: 'OrderDinners',
  data () {
    return {
      currentIndex: 0
    }
  },
  computed: {
    lists () {
      return this.$store.state.goodsList
    },
    categories () {
      const arr = []
      for (let item of this.lists) {
        arr.push(item.category)
      }
      return arr
    },
    distanceArr () {
      const arr = []
      const elementArr = document.querySelectorAll('.categoties-title')
      elementArr.forEach(ele => {
        arr.push(ele.offsetTop)
      })
      return arr
    }
  },
  methods: {
    addClass (index, event) {
      this.currentIndex = index
      const categories = document.querySelectorAll('.categoties-title')
      this.scroll.scrollToElement(categories[index], 600)
    },
    initScroll () {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.dinnerScroll, {
          click: true,
          probeType: 2
        })
        const _this = this
        this.scroll.on('scroll', function (params) {
          _this.distanceArr.forEach((value, key, arr) => {
            if (Math.abs(params.y) >= value && Math.abs(params.y) < arr[key + 1]) {
              _this.currentIndex = key
            }
          })
        })
      }
    }
  },
  components: {
    OrderFood
  },
  mounted () {
    this.$nextTick(() => {
      this.initScroll()
    })
    console.log(this.lists)
  },
  beforeDestroy () {
    this.scroll.destroy()
  }
}



